.imgDiv {
  display: inline-block;
  background-color: #3b3e46;
  margin: 5px;
  border: 0;
  position: relative;
}

.text {
  color: #545a64;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.1rem;
  text-transform: uppercase;
  text-anchor: middle;
  letter-spacing: 1px;
  font-weight: 600;
}

/* Home page button */

button:hover{cursor: pointer}
button {
  background: transparent; outline: none;
  position: relative;
  border: 2px solid #3efdd8;
  padding: 15px 50px;
  overflow: hidden;
}

/*button:before (attr data-hover)*/
#contactButton:hover:before{opacity: 1; transform: translate(0,0);}
#contactButton:before{
  content: attr(data-hover);
  position: absolute;
  top: 1.1em; left: 0;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 800;
  font-size: .8em;
  opacity: 0;
  transform: translate(-100%,0);
  transition: all .3s ease-in-out;
  color: #3efdd8;
}
/*button div (button text before hover)*/
#contactButton:hover div{opacity: 0; transform: translate(100%,0)}
#contactButton div{
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 800;
  font-size: .8em;
  transition: all .3s ease-in-out;
}

#contactText {
  color: #3efdd8;
}

